'use strict';

/* global $:true document:true d3:true mapboxgl: true window: true*/

$(document).ready(function () {
  // custom scripting goes here


  function drawPassGraphic(data) {
    console.log(data);
    var margin = {
      top: 0,
      right: 50,
      bottom: 40,
      left: 50
    };

    var width = $('#graphic').width();
    var height = 400;

    var x = d3.scaleLinear().range([0, width - margin.left - margin.right]);
    var y = d3.scaleLinear().range([height, 0]);

    x.domain([0, 100]);
    y.domain([height, 0]);

    var xAxis = d3.axisBottom().scale(x);

    var svg = d3.select('#graphic').data(data).append('svg').attr('width', width + margin.left + margin.right).attr('height', height + margin.top + margin.bottom);

    var line = function line(d) {
      var x1 = x(d.spot).toString();
      var x2 = x(d.spot + +d.yards).toString();
      var y1 = y(height).toString();
      var offset = y(height - +d.yards / 1.25 * 7).toString();
      var offsetX1 = x(d.spot + +d.yards / 4).toString();
      var offsetX2 = x(d.spot + +d.yards - +d.yards / 4).toString();

      return 'M ' + x1 + ',' + y1 + ' C ' + offsetX1 + ', ' + offset + ' ' + offsetX2 + ', ' + offset + ' ' + x2 + ', ' + y1;
    };

    var passes = svg.selectAll('g').data(data).enter().append('g').attr('transform', 'translate( ' + margin.left + ', ' + margin.top + ' )');

    passes.selectAll('path').data(function (d) {
      console.log(d);
      return d.plays;
    }).enter().append('path').attr('class', function (d) {
      if (d.touchdown === true) {
        return 'pass touchdown';
      }
      return 'pass';
    }).attr('d', function (d) {
      return line(d);
    });

    var base = svg.append('g');

    base.append('line').attr('x1', 0).attr('x2', width).attr('y1', height).attr('y2', height);

    svg.append('g').attr('class', 'x axis').attr('transform', 'translate(' + margin.left + ', ' + height + ')').call(xAxis);
  }

  $.getJSON('../js/data.json', function (data) {
    drawPassGraphic(data);
  });
});